@import '../abstracts/mediaQueries';
@import '../variables/colors';
@import '../variables/font';
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App {
  background-color: $color-main-1;
  // overflow:hidden;
  @include respond-to(tablet-landscape){
    // min-height:100%;
    // overflow-y: scroll;
  }
  @include respond-to(mobile-landscape){
    // max-height:100vh;
    // overflow-y: scroll;
  }
  // navbar & footer
  .navbar, .footer {
    p {
      font-size: $font-3;
    }
  }
  .navbar{
    color: $color-lighter;
  }
  .footer {
    color: $color-dark;
  }
  
  // main
  .main__container{
    font-size: $font-2;
    display:flex;
    height:90vh;
    max-height: 100vh;
    margin: 0;
    @include respond-to(lg-screen){
      flex-direction: column;
      height:auto;
      max-height:100%;
    }
    @include respond-to(tablet-landscape){
      min-height: 100vh;
    }
  }
  .input__container{
    display: block;
    text-align: left;
    width: 53.94vw;
    
    @include respond-to(lg-screen){
      // display: grid;
      grid-template-columns: 1.5fr 0.9fr;
      grid-template-rows: 0.5fr;
      gap: 2em 2em;
      grid-auto-flow: row;
      // align-content: space-evenly;
      justify-items: stretch;
      align-items: stretch;
      grid-template-areas:
        ". .";
      
      
      &-inner{
        margin: 0 auto;
        @include respond-to(lg-screen){
          width: 90vw;
        }
        @include respond-to(sm-screen){
          flex-direction: column;
        }
      }
    }
  }
}


