@import '../variables//colors';
@import '../variables/font';
.suggestions{
  display:flex;
  flex-wrap: wrap;
  padding: 1em;
  justify-content: space-between;
  align-content: stretch;
  align-items:stretch;
  max-width:80%;
  li {
    
    &:nth-child(even){
      color: $color-lighter;
    }
    &:hover{
      color: $color-dark;
    }
    max-width:100%;
    flex-grow: 1;
    color: $color-light;
    font-weight: 800;
    margin:.3em 0;
    font-size: 1.5rem;
  }
  .CitySearch__btn{
    position: absolute;
    top:2em;
    right:6em;
    margin: 0 1em 3em 0;
    
  }
}