@import '../variables/font';



.Event{
  max-width:100%;
  background-color: $color-lighter;
  
  border-radius: 7px;
  padding: 1.7em;
  margin: .8em 0;
  list-style-type: none;
  -webkit-border-radius: 7px;
  -moz-border-radius: 7px;
  -ms-border-radius: 7px;
  -o-border-radius: 7px;
}

.event{
  &-details-hide{
    display:none;
  }
  &-details-show{
    display:block;
    height:100%;
    
  }
  
  &-title{
    margin-bottom: 1.2em;
    cursor: pointer;
    
  }
  
  &-description{
    font-size: $font-1;
  }
  &-date, &-location{
    font-weight: 800;
  }
  
  &-link{
    margin: 2em 0;
    overflow: hidden;
    max-width: 10em;
  }
}